<template>
    <div class="bordered" v-bind:class="{ active: shown }">
        <div style="background-color: #888; color: #eff; padding: 2px 1rem; text-align: right;box-shadow: inset 0px -2px 4px 0px #0024;"> <a class="close" href="#" @click="$emit('close')"><i class="fa fa-times"></i></a></div>
        <div style="overflow: auto; flex: 1">
            <div class="pago" v-for="(pago, index) in lista_pagos" v-bind:key="index">
                <div v-bind="date_data=getDateData(pago.date)"><span>{{date_data.day_of_week}} {{date_data.day}}, {{date_data.month}} {{date_data.year}}</span></div>
                <div><span>{{pago.value}}</span></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ListaPagos',
    data:()=>({

    }),
    props:{
        lista_pagos : Array
    },
    computed:{
        shown() {return this.lista_pagos.length > 0;}
    },
    methods:{
        getDateData(item){
            const date = new Date(item);
            const days = [
                "Dom","Lun","Mar","Mie","Jue","Vie","Sab"
            ]
            const months = [
                'Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Setiembre', 'Octubre', 'Noviembre', 'Diciembre'
                ];
            return{
                day_of_week : days[date.getDay()],
                day : date.getDate(),
                month : months[date.getMonth()],
                year : date.getFullYear()
            }
        }
    }
}
</script>

<style>
.bordered{
    overflow: hidden;
    display: flex;
    flex-direction: column;

    border-radius: 2px;
    max-width: 400px;
    margin: auto;
    box-sizing: border-box;

    min-height: 0px;
    transition: max-height .5s ease-in-out;

    max-height: 0px;

    padding: 0 8px;
    background-color: #dfe;
}
.bordered.active{
    max-height: 360px;
    margin-bottom: 8px;
}
.close{
    text-decoration: none;
    font-weight: 700;
    font-size: 12px;
    color: #eff;
}
.pago{
    display:flex;
    overflow: hidden;

    margin: 1px;

    box-sizing: border-box;

    font-weight: 500;
    font-size: 13px;
}
.pago > div{flex: initial; padding: 2px 1rem; box-shadow: inset 0px -2px 4px 0px #0204;}
.pago > div:last-child{
    background-color: #2a4;
    text-align: right;
    color: #dfe;
    min-width: 55px;
    font-weight: 600;
}
.pago > div:first-child{
    background-color: #afb;
    flex: 1;
    text-align: left;
}
</style>