<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png">
    <div>
      <ListaPagos :lista_pagos="lista_pagos" :shown="shown" @close="lista_pagos=[]"/>
      <BondList :forview="true" :bonos="$store.state.cartera_bonos"
        @compare="bondlistAction(0, $event)"
        @delete="bondlistAction(1, $event)"
        @view="inspect($event)"/>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import * as finance from '../assets/finance';
import BondList from '../components/BondList.vue'
import ListaPagos from '../components/ListaPagos.vue';


export default {
  name: 'Home',
  data: ()=>({
    bono : null,
    lista_pagos: [],
    shown: false
  }),
  components: {
    BondList,
    ListaPagos
  },
  methods:{
    bondlistAction: function(type, event){
      let aux_arr = [];
      switch (type) {
        case 0:
          this.$store.dispatch('pre_load');

          event.forEach((element, index) => {
            if(element) aux_arr.push(this.$store.state.cartera_bonos[index]);
            });

          this.$store.state.aux_bonos = aux_arr;
          this.$router.replace('/comparator');
        break;
        case 1:
          this.$store.dispatch('pre_load');

          event.forEach((element, index) => {
            if(!element) aux_arr.push(this.$store.state.cartera_bonos[index]);
            });

          this.$store.state.aux_cartera = aux_arr;
          this.$store.dispatch('post_bonos');
        break;
      }
    },
    inspect(index){
      this.bono = this.$store.state.cartera_bonos[index];
      this.lista_pagos = finance.default.BonoPagos(this.bono);
      console.log(this.lista_pagos);
    }
  },
  mounted(){
  }
}
</script>
