export default{
    BonoCalculadora: (_valornominal, _tasacupon, _frecuencia, _periodo, _tasamercado, _prima) =>{
        _tasacupon/= 100;
        _tasamercado/=100;
        _prima/=100;
        
        let _ncapitalizaciones = _periodo * (360/_frecuencia);
        
        let _tasaefectivacupon = Math.pow(1+_tasacupon, _frecuencia / 360) - 1;
        let _tasaefectivamercado = Math.pow(1+_tasamercado, _frecuencia / 360) - 1;
        
        let _icupon = Math.round(_valornominal * _tasaefectivacupon * 100) / 100;
        
        let _VNA = 0;
        let _pagos = [];
        
        for(let i = 1; i <= _ncapitalizaciones; i++){
            let aux = ( i == _ncapitalizaciones ? (_icupon + _valornominal*(1 + _prima)) : _icupon ) / Math.pow(1 + _tasaefectivamercado, i);
            _VNA += aux;
            _pagos.push(aux);
        }
        
        return {
            VNA : Math.round(_VNA * 100) / 100,
            pagos : _pagos,
            cupon : _icupon
        }
    },    
    BonoAnalisis: (_bonocalculado, _valorcomercial, _CAVALI = 0, _flotaci贸n = 0) =>{
        _CAVALI /= 100;
        _flotaci贸n /= 100;
        let _costo = _valorcomercial * (1 + _CAVALI + _flotaci贸n);
        let _va = _bonocalculado.pagos.reduce((a,b)=>{return a + b}) - _costo;

        return {
            precio_de_venta : Math.round( 100 *_costo) / 100,
            valor_de_venta : _bonocalculado.VNA,
            VNA : Math.round( 100 * _va) / 100
        }
    },
    BonoTIR:(bono)=>{
        console.log("calculando")
        console.log(bono);

        let result;
        let tries = 0;
        let _va = bono.precio_actual;
        let guess = .01;

        let limUP = .5;
        let limDOWN = .0001;
        let pagos = []
        do{
            tries++;
            result = 0;
            pagos.length = 0;

            for (let i=0;  i < bono.cxp; i++) {
                pagos.push((i != bono.cxp - 1) ? bono.cupon : (bono.cupon + bono.valor_nominal * (1+bono.prima)));
                result += ((i != bono.cxp - 1) ? bono.cupon : (bono.cupon + bono.valor_nominal * (1+bono.prima))) / Math.pow((1 + guess), i + 1);
            }

            let oldguess = guess;
            if(Math.sign(result - _va) > 0)
                limDOWN = guess;
            else
                limUP = guess;
            

            guess = limDOWN + (limUP - limDOWN)*.5;
            console.log({
                oldguess,
                guess,
                result,
                diff : result - _va,
                _va
            });
        }while(Math.abs(result - _va) > .0005 && tries < 50);

        console.log(pagos);
        return guess;
    },
    BonoPagos: (_bono) =>{
        const now = new Date(_bono.date);
        var _frecuencia = _bono.frecuencia;
        const _periodo = _bono.perido;
        var dates = [];
        
        var tipo_cap = 0;

        console.log(_bono);

        if (_frecuencia < 360){
            tipo_cap = 2;
            _frecuencia = _frecuencia / 30;
        }else{
            tipo_cap = 3;
            _frecuencia = _frecuencia / 360;
        }

        switch(tipo_cap){
            case 2:
                for(let i = 0; i < _bono.cxp; i++){
                    dates.push( {
                            date : new Date(now.getFullYear(), now.getMonth() + _frecuencia*i, now.getDay()).toDateString(),
                            value : (i != _bono.cxp - 1) ? _bono.cupon : (_bono.cupon + _bono.valor_nominal * (1+_bono.prima))
                        });
                }
                break;
            case 3:
                for(let i = 0; i < _bono.cxp; i++){
                    dates.push( {
                            date : new Date(now.getFullYear()  + _frecuencia*i, now.getMonth(), now.getDay()).toDateString(),
                            value : (i != _bono.cxp - 1) ? _bono.cupon : (_bono.cupon + _bono.valor_nominal * (1+_bono.prima))
                        });
                }
                break;
        }
        return dates;
    }
}